.no-border {
  border: none !important;
}

.u-mt {
  margin-top: 15px !important;
}

/*LANDR colors*/
.neutral {
  background-color: #0c1b2b !important;
}

.primary {
  background-color: #32cbcb !important;
}

.warning {
  background-color: #eec200 !important;
}
.warning-text {
  color: #eec200 !important;
}

.danger {
  background-color: #f8645c !important;
}
.danger-text {
  color: #f8645c !important;
}

.success {
  background-color: #71d9a3 !important;
}

.tag {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  font-size: 13px;
  line-height: 36px;
  overflow: hidden;
  padding: 0 16px;
  border-radius: 3px;
  margin: 5px 5px 5px 0;
  border: 2px solid #7f7f7f;
  color: #7f7f7f;
}
.tag.correct {
  border-color: #71d9a3;
  color: #71d9a3;
}
.tag.incorrect {
  border-color: #f8645c;
  color: #f8645c;
}
.tag > i {
  line-height: inherit;
}

.collapsible {
  overflow: hidden;
  border-radius: 4px;
}

header {
  text-align: center;
  -webkit-transition: 0.3s padding ease-out;
  transition: 0.3s padding ease-out;
}
.logo {
  width: 50vmin;
}
.progress {
  width: 50vmin;
  display: inline-block;
}

.entry-rank {
  line-height: 36px;
  height: 36px;
  width: 36px;
  border-radius: 50%;
  text-align: center;
  margin-right: 1rem;
  font-weight: 700;
}

.entry-name {
  font-size: 16px;
  font-weight: 500;
}

.prediction:not(:first-of-type) {
  padding-top: 5px;
}

